.messageInput{
    display: inline-block;
    box-sizing: border-box;
    box-shadow: none;
    border: 0;
    outline: 0;
    -webkit-appearance: none;
    appearance: none;
    text-overflow: ellipsis;
    line-height: 19px;
    font-size: 16px;
    color: var(--text_primary);
    width: calc(100% - 98px);
    position: relative;
    top: -7px;
    left: 4px;
    z-index: 2;
    background: 0 0;
}

.typing__loading.Loading {
    min-height: 16px;
}

.typing__loading {
    width: 40px;
    height: 16px;
    margin-right: 8px;
    -webkit-flex-shrink: 0;
    -ms-flex-negative: 0;
    flex-shrink: 0;
}
.Loading {
    min-height: 32px;
    display: -webkit-box;
    display: -webkit-flex;
    display: -moz-box;
    display: -ms-flexbox;
    display: inline-flex;
    -webkit-box-align: center;
    -webkit-align-items: center;
    -moz-box-align: center;
    -ms-flex-align: center;
    align-items: center;
    -webkit-box-pack: center;
    -webkit-justify-content: center;
    -moz-box-pack: center;
    -ms-flex-pack: center;
    justify-content: center;
}
.typing__loading .Loading__item:not(:last-child) {
    margin-right: 3px;
}
.Loading__item_0 {
    -webkit-animation-delay: 0ms;
    -moz-animation-delay: 0ms;
    -o-animation-delay: 0ms;
    animation-delay: 0ms;
}
.Loading__item {
    float: left;
    width: 5px;
    height: 5px;
    margin-right: 5px;
    -webkit-border-radius: 50%;
    -moz-border-radius: 50%;
    border-radius: 50%;
    background-color: #45678f;
    -webkit-flex-shrink: 0;
    -ms-flex-negative: 0;
    flex-shrink: 0;
    -webkit-box-flex: 0;
    -webkit-flex-grow: 0;
    -moz-box-flex: 0;
    -ms-flex-positive: 0;
    flex-grow: 0;
    opacity: 0.2;
    -webkit-animation-name: Loading-animation;
    -moz-animation-name: Loading-animation;
    -o-animation-name: Loading-animation;
    animation-name: Loading-animation;
    -webkit-animation-duration: 750ms;
    -moz-animation-duration: 750ms;
    -o-animation-duration: 750ms;
    animation-duration: 750ms;
    -webkit-animation-iteration-count: infinite;
    -moz-animation-iteration-count: infinite;
    -o-animation-iteration-count: infinite;
    animation-iteration-count: infinite;
}
.Loading__item_0 {
    -webkit-animation-delay: 0ms;
    -moz-animation-delay: 0ms;
    -o-animation-delay: 0ms;
    animation-delay: 0ms;
}
.Loading__item_1 {
    -webkit-animation-delay: 180ms;
    -moz-animation-delay: 180ms;
    -o-animation-delay: 180ms;
    animation-delay: 180ms;
}
.Loading__item_2 {
    -webkit-animation-delay: 360ms;
    -moz-animation-delay: 360ms;
    -o-animation-delay: 360ms;
    animation-delay: 360ms;
    margin-right: 0;
}
@-webkit-keyframes Loading-animation{0%,to{opacity:0.2}30%{opacity:1}}@-moz-keyframes Loading-animation{0%,to{opacity:0.2}30%{opacity:1}}@-o-keyframes Loading-animation{0%,to{opacity:0.2}30%{opacity:1}}@keyframes Loading-animation{0%,to{opacity:0.2}30%{opacity:1}}